/* animation */
.entrance--container{
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color:black;
}
.entrance--wrapper{

  position: relative;
  width: 100%;
  min-height: 100%;
  transform-style: preserve-3d;
  display: flex;
  align-items: center;
  justify-content: center;
}
.bubble--wrapper{
  margin: 0 auto;
  width: 500px;
  height: 500px;
  transform-style: preserve-3d;
  transform-origin: center center;
  perspective: 800px;
}
.entrance--title__container{
  position: fixed;
  left:0;
  top:0;
  display:flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  transform-style: preserve-3d;
  perspective: 2000px;
}
.qrcode_container{
    width: 100%;
    /* min-height: 100vh; */
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 200px;
}
.entrance--title__wrapper{
  position: absolute;
  transform:translate3d(0 ,0 ,-4000px);
  display:flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 100px;
}
.entrance--title{
  font-size: 25px;
  letter-spacing: 10px;
}
.entrance--subtitle{
  font-size: 70px;
  bottom:0;
}
.namesCount_container{
  position:fixed;
  bottom:0;
  left:50%;
  transform:translateX(-50%);
  width: 200px;
  color:rgba(255, 255, 255, 0.4);
  font-size: 12px;
  text-align: center;
}
/* @-webkit-keyframes move {
  100% {
    transform: translate3d(50%, -50%, -400px);
  }
} */
.loading_container{
  overflow: hidden;
  position: relative;
  height: 0;
  position: 'absolute';
  min-height: 100%;
  left: 50%;
  top: 50%;
  transform:translate(-50%,-50%);
  z-index:10000;
  width:100%;
  background-color:#2c2c2c;
}

@keyframes fadeout {
  90% {opacity: 1;}
  100% { opacity:0 }
}
@keyframes tilt {
  25% { transform: rotateX(25deg) rotateY(25deg); }
  50% { transform: rotateX(25deg) rotateY(-25deg); }
  75% { transform: rotateX(-25deg) rotateY(25deg); }
}
@keyframes colorSwap {
  25% { --color: cyan; }
  50% { --color: magenta; }
  75% { --color: lime; }
}
@keyframes spin {
  0% {  rotate:(0deg) }
  100% { transform:translate(-50%,-50%) rotate(360deg) }
}
@keyframes bigtosmall{
  0%{
    transform: scale(6);
  }
  30%{
    transform: scale(5);
  }
  100%{
    transform: scale(0.1);
  }
}
@keyframes move {

  60%{
    opacity: 1;
  }
  80%{
    opacity: 0;
  }
  100% {
    transform: translate3d(50%, 100%, -2000px) scale(0.1);
    opacity: 0;
  }
}